import React from 'react';
import Icon from '@material-ui/core/Icon';
import Card from '@mui/material/Card';
import TextArea from 'react-textarea-autosize';
// import CardActions from '@mui/material/CardActions';
// import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import styled from 'styled-components';
// import Typography from '@mui/material/Typography';
import { connect } from "react-redux";
import { addList, addCard } from "../actions";

const  openFormButtonGroup = styled.div`
    display: "flex";
    align-items: "center";
    cursor: "pointer";
    border-radius: 3px;
    height: 36px;
    width: 300px;
    padding-left: 10px;
  `
const formButtonGroup = styled.div`
    margin-top: 8px;
    display: flex;
    alignItems: center;
`
class ActionButton extends React.Component {
  state = {
    formOpen: false,
    text: ""
  }

  openForm = () => {
    this.setState({formOpen: true});
  }

  closeForm = (e) => {
    this.setState({formOpen: false});
  }

  handleInputChange = (e) => {
    this.setState({
      text: e.target.value
    })
  }

  handleAddList = () => {
    const { dispatch } = this.props;
    const {text} = this.state;

    if (text) {
      this.setState({
        text: ""
      })
      dispatch(addList(text))

    }

    return;
  }

  handleAddCard = () => {
    const { dispatch, listId } = this.props;
    const { text } = this.state;
    if (text) {
      this.setState({
        text: ""
      })
      dispatch(addCard(listId, text))
    }

    return;
  }

  renderAddButton = () => {
    const { list } = this.props;
    const buttonText = list ? "Add another list" : "Add another card";
    const buttonTextOpacity = list ? 1 : 0.5;
    const buttonTextColor = list ? "white" : "inherit";
    const buttonTextBackground = list ? "rgba(0,0,0,.15)" : "inherit";
    return (
        <div 
              onClick={this.openForm}
              style={{
                    ...styles.openFormButtonGroup,
                    opacity:  buttonTextOpacity, 
                    color: buttonTextColor, 
                    background: buttonTextBackground
              }}>
        <Icon>add</Icon>
        <p>{buttonText}</p>
        </div>
    )
  }

  renderForm = () => {
    const { list } = this.props;

    const placeholder = list ? "Enter list title..." : "Enter title for this card...";
    const buttonTitle = list ? "Add List" : "Add Card";
    return (
    <div> 
      <Card style={{
                      overflow: "visible",
                      minHeight: 80,
                      maxWidth: 284,
                      padding: "6px 8px 2px",
                      marginBottom: 4
                  }}>
        <TextArea placeholder={placeholder} 
                  autoFocus onBlur={this.closeForm} 
                  value={this.state.text} 
                  onChange={this.handleInputChange}
                  style={{
                    resize: "none",
                    // width: "100%",
                    outline: "none",
                    border: "none",
                    
                  }}
                  />
      </Card>
      <formButtonGroup>
        {/* We use onMouseDown because it will fire before our onBlur function. */}
        <Button variant="contained" 
                style={{color: "white", backgroundColor:"#5aac44"}}
                onMouseDown={list ? this.handleAddList : this.handleAddCard}>
        {buttonTitle}
        </Button>
        <Icon style={{marginLeft: 8, cursor: "pointer", color: "red"}} onClick={this.closeForm}>close</Icon>
      </formButtonGroup>
    </div>
    )
  }

  render () {
    return this.state.formOpen ? this.renderForm() : this.renderAddButton();
  }
}

const styles = {
  openFormButtonGroup: {
    display: "flex",
    alignItems: "center",
    cursor: "pointer",
    borderRadius: 3,
    height: 36,
    width: 300,
    paddingLeft: 10
  },
  formButtonGroup: {
    marginTop: 8,
    display: "flex",
    alignItems: "center"
  }
}


export default connect() (ActionButton);