import { black } from 'material-ui/styles/colors';
// import {boxShadow} from 'jss-plugin-expand';
import React from 'react';
import Card from './TrelloCard';
import ActionButton from './ActionButton';
import {Droppable, Draggable} from 'react-beautiful-dnd';
import styled from "styled-components";

const List = styled.div`
    background-color: #dfe3e6;
    border-radius: .4em;
    width: 300px;
    height: 100%;
    padding: .5em;
    margin-right: .5em;

    @media (max-width: 500px) {
      width: calc(100vw - 7em);
      margin-bottom: 1em;
    }
    @media (max-width: 400px) {
      width: calc(100vw - 5em);
    }
`

const Title = styled.h4`
  font-family: "Roboto";
`

const TrelloList = ({title, cards, listId, index}) => {
  
  return (
    <Draggable draggableId={String(listId)} index={index}>
      {(provided) => (
    <List {...provided.draggableProps} ref={provided.innerRef} {...provided.dragHandleProps}>
    <Droppable droppableId={String(listId)}>
      {/* Everything inside a droppable must be within a React render props function */}
      {(provided) => (
         <div {...provided.droppableProps} ref={provided.innerRef}> 
          <Title>{title}</Title>
          {cards.map((card, index) => (< Card message={card.text} key={card.id} id={card.id} style={styles.card} index={index} />
          ))}
          {provided.placeholder}
          < ActionButton listId={listId} />
        </div> 
      )}
    </Droppable>
    </List>
    )}
    </Draggable>
    
  )
}

const styles = {
  container: {
    backgroundColor: "#dfe3e6",
    borderRadius: 3,
    width: 300,
    height: "100%",
    padding: 8,
    marginRight: 8,
    marginBottom: 8
  },

  title: {
    fontFamily: "Roboto"
  },

  card: {
    marginBottom: 8
  }
}

export default TrelloList;