import {CONSTANTS} from "../actions";
let listId = 2;
let cardId = 5;
const initialState = [
  {
    title: "(F)rello",
    id: `list-${0}`,
    cards: [
      {
        id: `card-${0}`,
        text: "This is a project where I wanted to work on creating interable UIs using React."
      },
      {
        id: `card-${1}`,
        text: "I used React, React-Redux, react-beautiful-dnd and Material UI to create this."
      }
    ]
  },
  {
    title: "Limitations",
    id: `list-${1}`,
    cards: [
      {
        id: `card-${2}`,
        text: "This is just a small project and I haven't yet hooked it up to a backend to persist the lists."
      },
      {
        id: `card-${3}`,
        text: "I plan to expand on this by adding the ability to create multiple boards and switch between them using an expandable sidebar."
      },
      {
        id: `card-${4}`,
        text: "You can add new cards, create a new list and move cards and lists around the area."
      }
    ]
  }  
]

const listsReducer = (state = initialState, action) => {
  switch(action.type) {
    case CONSTANTS.ADD_LIST:
      const newList = {
        title: action.payload,
        cards: [],
        id: `list-${listId}`
      }
      listId += 1;
      return [...state, newList];

    case CONSTANTS.ADD_CARD: {
      const newCard = {
        text: action.payload.text,
        id: `card-${cardId}`
      }
      cardId+= 1;
      console.log("Action received", action);
      const newState = state.map(list => {
        if(list.id === action.payload.listId) {
          return {
            ...list,
            cards: [...list.cards, newCard]
          }
        } else {
          return list;
        }
      })
      return newState;
    }

    case CONSTANTS.DRAG_HAPPENED:
      const {
        droppableIdStart,
        droppableIdEnd,
        droppableIndexStart,
        droppableIndexEnd,
        draggableId,
        type
      } = action.payload;
      const newState = [...state];
      //Dragging list

      if(type === "list") {
        const list = newState.splice(droppableIndexStart, 1);
        newState.splice(droppableIndexEnd, 0, ...list);
        return newState;
      }

      //When card is dropped within the same list
      if (droppableIdStart === droppableIdEnd) {
        const list = state.find(list => droppableIdStart === list.id);
        const card = list.cards.splice(droppableIndexStart, 1);
        list.cards.splice(droppableIndexEnd, 0, ...card)
      }

      //When dropped into another list
      if (droppableIdStart !== droppableIdEnd) {
        //Find id of the list where the drag started
        const listStart = state.find(list => droppableIdStart === list.id);
        //Remove the card from the cards array
        const card = listStart.cards.splice(droppableIndexStart, 1);
        //Find the list where the drag ended
        const listEnd = state.find(list => droppableIdEnd === list.id);

        //Put the card in the new list
        listEnd.cards.splice(droppableIndexEnd, 0, ...card)
      }
      return newState;
       
      
    default:
      return state;
  }
}

export default listsReducer;