import React from "react";
import styled from "styled-components";

const HeaderContainer = styled.div`
  background-color: #343880;
  height: 3em;
  min-width: calc(100vw -1em);
  color: white;
  display: flex;
  align-items: center;
  padding-left: 1em;
`

const HeaderBrand = styled.span`
  font-family: Roboto;
  font-size: 1.5em;
  font-weight: 500;
`


class Header extends React.Component {
  render () {
    return (
      <HeaderContainer>
        <HeaderBrand>Frello</HeaderBrand>
        </HeaderContainer>
    );
  }
}

export default Header;